import html2canvas from 'html2canvas';
export default {
    name: 'dispatchCenter',
    data() {
        return {
            model1: '', //
            routeList: [],
            success_number: null, //
            wait_number: null, //
            columns1: [
                {
                    title: '',
                    key: 'order_sn',
                    tooltip: true,
                    align: 'center'
                },
                {
                    title: '',
                    key: 'route',
                    align: 'center',
                },
                {
                    title: '',
                    key: 'name',
                    render: (h, params) => {
                        let len = params.row.name.length - 1;
                        let xing = '';
                        for (let i = 0; i < len; i++) {
                            xing += '*';
                        }
                        let name1 = params.row.name.substring(0, 1) + xing
                        return h('div', [
                            h('Icon', {
                                props: {
                                    type: 'person'
                                }
                            }),
                            h('strong', name1)
                        ]);
                    },
                    align: 'center',
                },
                {
                    title: '',
                    key: 'phone',
                    tooltip: true,
                    align: 'center'
                },
                {
                    title: '',
                    key: 'date',
                    align: 'center'
                },
                {
                    title: '',
                    key: 'time',
                    align: 'center',
                },
                {
                    title: '',
                    key: 'driver_name',
                    align: 'center',
                },
                {
                    title: '',
                    key: 'car_no',
                    align: 'center'
                },
                {
                    title: '',
                    slot: 'action',
                    width: 150,
                    align: 'center'
                }
            ], //table title

            columns15: [
                {
                    title: "",
                    key: "name"
                },
                {
                    title: "",
                    key: "price"
                },
                {
                    title: "",
                    key: "commission"
                }
            ],//title

            columns2: [
                {
                    title: '',
                    key: 'order_sn',
                    tooltip: true,
                    align: 'center',
                    width: 200
                },
                {
                    title: '',
                    key: 'route',
                    align: 'center',
                },
                {
                    title: '',
                    key: 'name',
                    render: (h, params) => {
                        let len = params.row.name.length - 1;
                        let xing = '';
                        for (let i = 0; i < len; i++) {
                            xing += '*';
                        }
                        let name1 = params.row.name.substring(0, 1) + xing
                        return h('div', [
                            h('Icon', {
                                props: {
                                    type: 'person'
                                }
                            }),
                            h('strong', name1)
                        ]);
                    },
                    align: 'center',
                    width: 80
                },
                {
                    title: '',
                    key: 'phone',
                    tooltip: true,
                    align: 'center'
                },
                {
                    title: '',
                    key: 'date',
                    align: 'center'
                },
                {
                    title: '',
                    key: 'time',
                    align: 'center',
                },
                {
                    title: '',
                    key: 'driver_name',
                    align: 'center',
                },
                {
                    title: '',
                    key: 'car_no',
                    align: 'center'
                }
            ],//table title
            data11: [],//
            data1: [],  //table
            data2: [],  //table
            currentDate: '', //
            classData: [], //
            clickIndex: 0, //index
            firstClassDataId: null, //id
            getOrderListId: null, //id
            detailInfoModal: false, //
            detailContent: {}, //
            detailInfo: {},
            routeId: null,
            isExport: true, //
            exportInfo: {}, //

        }
    },
    components: {
        html2canvas
    },
    created() {
        this.$Spin.show();
    },
    mounted() {
        //
        this.getDay()
        //
        this.selectRouteList()
        //-
    },
    methods: {
        //
        getDay() {
            let date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            let currentdate = year + '-' + month + '-' + strDate;
            this.currentDate = currentdate;
            this.$Spin.hide();
        },
        //
        selectRouteList() {
            let that = this
            this.$fetch("/admin/index/select-route").then(res => {
                let list = Object.keys(res.data).map((item, index) => ({ value: item, label: res.data[item] }))
                that.routeList = list
                console.log(list)
                that.model1 = list[0].value
                that.firstClassDataId = list[0].value
                this.routeId = list[0].value
                //id
                this.getRouteClass(list[0].value)

            })
        },

        //
        getRouteClass(route_id) {
            let that = this
            this.$fetch('/admin/index/get-dispatch-classes', {
                route_id
            }).then(res => {
                console.log(res)
                console.log(res.data.order)
                if (res.code == 200) {
                    that.success_number = res.data.order.success_number //
                    that.wait_number = res.data.order.wait_number //
                    if(res.data.list.length>0){
                        //
                        that.classData = res.data.list
                        //
                        //
                        console.log(that.classData[0])
    
                        this.getOrderListId = that.classData[0].id
                        console.log(this.getOrderListId)
                        console.log(that.classData[0].id)
                        this.$fetch('/admin/index/get-dispatch-order', {
                            id: that.classData[0].id
                        }).then(res => {
                            console.log(res)
                            if (res.code == 200) {
                                that.data1 = res.data
                            } else {
                                that.$Message.warning(res.error)
                            }
                        })

                    }else{
                        that.$Message.info('')
                        that.classData = []
                        that.data1 = []
                    }

                } else {
                    that.$Message.warning(res.error)
                }

            })
        },

        //
        routeClassChange(e) {
            console.log(e) //evaluelabel
            let route_id = parseInt(e.value)
            this.routeId = route_id
            this.getOrderListId = null
            //,
            this.getRouteClass(route_id)
        },

        //,table
        clickRouteTime(index, item) {
            let that = this
            console.log(item.id)
            this.clickIndex = index
            this.getOrderListId = item.id
            this.$fetch('/admin/index/get-dispatch-order', {
                id: item.id
            }).then(res => {
                console.log(res)
                if (res.code == 200) {
                    that.data1 = res.data
                } else {
                    that.$Message.warning(res.error)
                }
            })
        },
        //
        refresh() {
            console.log(this.routeId)
            let that = this
            // this.getRouteClass(this.routeId)
            // id id: this.getOrderListId

            console.log(this.getOrderListId)
            this.$Spin.show();
            if(this.getOrderListId == null){
                //idnull
                this.$Spin.hide();
                this.$Message.info('')
            }else{
                this.$fetch('/admin/index/get-dispatch-order', {
                    id: this.getOrderListId
                }).then(res => {
                    console.log(res)
                    if (res.code == 200) {
                        that.data1 = res.data
                        this.$Spin.hide();
                    } else {
                        this.$Spin.hide();
                        that.$Message.warning(res.error)
                    }
                })
                console.log(this.routeId)
                this.$fetch('/admin/index/get-dispatch-classes', {
                    route_id: that.routeId
                }).then(res => {
                    console.log(res)
                    if (res.code == 200) {
                        that.success_number = res.data.order.success_number //
                        that.wait_number = res.data.order.wait_number //
                        that.classData = res.data.list
                    } else {
                        that.$Message.warning(res.error)
                    }
    
                })
            }

        },

        //
        show(row) {
            console.log(row)
            this.detailInfoModal = true
            // this.detailInfo = row
            this.modal1 = true;
            this.$fetch('/admin/order/detail', {
                order_id: row.id
            }).then(res => {
                if (res.code === 200) {
                    console.log(res)
                    this.detailInfo = res.data
                    //
                    if (this.detailInfo.cancel_time !== 0) {
                        this.detailInfo.cancel_time = this.timestampToTime(this.detailInfo.cancel_time)
                    } else if (this.detailInfo.refund_time !== 0) {
                        //
                        this.detailInfo.refund_time = this.timestampToTime(this.detailInfo.refund_time)
                    }
                    //
                    if (this.detailInfo.change_ticket_time !== 0) {
                        this.detailInfo.change_ticket_time = this.timestampToTime(this.detailInfo.change_ticket_time)
                    }
                    //
                    this.detailInfo.ticket_time = this.timestampToTime(this.detailInfo.ticket_time)
                    //
                    if (this.detailInfo.status == 1) {
                        this.detailInfo.status = ''
                        this.detailInfo['orderType'] = ''
                    } else if (this.detailInfo.status == 3 || this.detailInfo.status == 2) {
                        this.detailInfo.status = ''
                        this.detailInfo['orderType'] = ''
                    } else if (this.detailInfo.status == 9) {
                        this.detailInfo.status = ''
                        this.detailInfo['orderType'] = ''
                    } else if (this.detailInfo.status == 8) {
                        this.detailInfo.status = ''
                        this.detailInfo['orderType'] = ''
                    } else if (this.detailInfo.status == 6) {
                        this.detailInfo.status = ''
                        this.detailInfo['orderType'] = ''
                    } else if (this.detailInfo.status == 5) {
                        this.detailInfo.status = ''
                        this.detailInfo['orderType'] = ''
                    } else if (this.detailInfo.status === 11) {
                        this.detailInfo.status = '',
                            this.detailInfo['orderType'] = ''
                    }
                    let data11 = [

                    ]
                    res.data.order.forEach(res => {
                        console.log(res)
                        res.commission = (res.commission / 100).toFixed(2)
                        res.price = (res.price / 100).toFixed(2)
                        data11.push(res)
                    })
                    this.data11 = data11
                    console.log(this.detailInfo)
                }
            }).catch(err => {
                this.$Spin.hide();
            })
        },

        //
        remove(index, row) {
            console.log(row)
            console.log(this.routeId)
            this.$fetch('/admin/order/detail', {
                order_id: row.id
            }).then(res => {
                console.log(res)
                if (res.code == 200) {
                    let row = res.data
                    if (row.status == 2) {
                        this.$Modal.confirm({
                            content: `?<br>,`,
                            onOk: () => {
                                let params = {
                                    order_sn: String(row.order_sn),
                                    uid: String(row.uid)
                                }
                                this.$fetch("/admin/order/refund", params).then(res => {
                                    if (res.code == 200) {
                                        this.$Message.success({
                                            content: '',
                                            duration: 2
                                        })
                                        this.refresh()
                                        // this.getRouteClass(this.routeId)

                                    } else {
                                        this.$Message.error({
                                            content: res.error,
                                            duration: 2
                                        })
                                    }
                                }).catch(err => {
                                    this.$Spin.hide();
                                    // this.$Message.warning("")
                                })
                            }
                        });
                    } else if (row.status == 8 || row.status == 5) {
                        this.$Message.error({
                            content: '',
                            duration: 2
                        })
                        this.refresh()
                        // this.getRouteClass(this.routeId)
                    } else if (row.status == 1) {
                        this.$Modal.confirm({
                            content: `?`,
                            onOk: () => {
                                let params = {
                                    order_sn: String(row.order_sn),
                                }
                                this.$fetch("/admin/order/cancel", params).then(res => {
                                    if (res.code == 200) {
                                        this.$Message.success({
                                            content: '',
                                            duration: 2
                                        })
                                        this.refresh()
                                        // this.getRouteClass(this.routeId)
                                    } else {
                                        this.$Message.error({
                                            content: res.error,
                                            duration: 2
                                        })
                                    }
                                }).catch(err => {
                                    this.$Spin.hide();
                                })
                            }
                        });
                    }

                } else {
                    this.$Message.error(res.error)
                }
            })


        },

        //
        timestampToTime(timestamp) {
            var date = new Date(timestamp * 1000);//10*1000131000
            var Y = date.getFullYear() + '-';
            var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
            var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
            var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
            var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
            var s = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
            return Y + M + D + h + m + s;
        },

        //
        exportImg() {
            console.log(this.getOrderListId)
            if(this.data1.length<=0){
                //,
                this.$Message.info('')
            }else{
                this.isExport = false
                //id
                this.$fetch('/admin/index/export-dispatch-data', {
                    id: this.getOrderListId
                }).then(res => {
                    console.log(res)
                    if (res.code == 200) {
                        this.exportInfo = res.data.info //
                        this.data2 = res.data.list
                    } else {
                        that.$Message.warning(res.error)
                    }
                })
            }
        },

        //
        exportPic() {
            html2canvas(this.$refs.pic, {
                backgroundColor: '#fff',
                scale: 2,
                dpi: window.devicePixelRatio // 
            }).then((canvas) => {
                // console.log(canvas)
                let image = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream");
                // console.log(image)
                let userAgent = navigator.userAgent;
                //
                let name = this.exportInfo.ticket_time
                console.log(name)
                // ie
                if (userAgent.includes("Trident")) {
                    let arr = image.split(',');
                    let mime = arr[0].match(/:(.*?);/)[1];
                    let bstr = atob(arr[1]);
                    let n = bstr.length;
                    let u8arr = new Uint8Array(n);
                    while (n--) {
                        u8arr[n] = bstr.charCodeAt(n);
                    }
                    window.navigator.msSaveBlob(new Blob([u8arr], { type: mime }), `${name}.jpg`);
                } else {
                    let imgData = canvas.toDataURL("image/jpeg");
                    let aLink = document.createElement("a");
                    aLink.style.display = "none";
                    aLink.href = imgData;
                    aLink.download = `${name}.jpg`;
                    document.body.appendChild(aLink);
                    aLink.click();
                    document.body.removeChild(aLink);

                }

             

                // let url = canvas.toDataURL('image/png');
                // // console.log(url)
                // // a
                // let a = document.createElement('a')
                // // 
                // let event = new MouseEvent('click')
                // // adownloadname
                // a.download = name || ''
                // // URLa.href
                // a.href = url
                // // a
                // a.dispatchEvent(event)
            })
        }


    }
}


