<template>
  <!--  -->
  <div class="dispatchCenter" v-if="isExport">
    <div class="title">
      <div class="shu"></div>
      <h4> ({{currentDate}})</h4>
    </div>

    <div class="select_route">
      <div class="select_item">
        <Select
          v-model="model1"
          style="width:150px"
          @on-change="routeClassChange"
          :label-in-value="true"
        >
          <Option v-for="item in routeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </div>
      <div class="people_num">
        
        <span>
          <b>{{success_number}}</b>
        </span>
      </div>
      <div class="people_num">
        
        <span>
          <b>{{wait_number}}</b>
        </span>
      </div>
    </div>

    <div class="route_time">
      <!--   .click_bg -->

      <div v-if="classData.length > 0" style="display: flex; align-items: center;">
        <div
          :class="['route_time_item', {'click_bg':index==clickIndex}]"
          @click="clickRouteTime(index,item)"
          v-for="(item,index) in classData"
          :key="index"
        >
          <div>
            <p>{{item.route}}</p>
            <p>{{item.time}}</p>
          </div>
          <div class="order_num">{{item.number}}</div>
        </div>
      </div>

      <div v-else class="route_time_item"></div>
    </div>

    <div class="btns">
      <Button @click="refresh"></Button>
      <Button type="primary" @click="exportImg"></Button>
    </div>

    <!-- table -->
    <Table :columns="columns1" :data="data1" border>
      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" @click="show(row)"></Button>
        <Button type="error" size="small" @click="remove(index,row)"></Button>
      </template>
    </Table>

    <!--  -->
    <Modal v-model="detailInfoModal" title="" ok-text="" cancel-text width="800">
      <div class="ModalBox">
        <div class="boxLine">
          <div class="boxList">
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.order_sn}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.change_ticket_time || ''}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.created_at}}</div>
            </div>
          </div>
          <div class="boxList">
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.status}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.cancel_time || detailInfo.refund_time || ''}}</div>
            </div>
          </div>
          <div class="boxList">
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.orderType}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.cancel_reason}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="ModalBox">
        <div class="boxLine">
          <div class="boxList">
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.from}}-{{detailInfo.to}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.name}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.driver_name}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.start_name}}</div>
            </div>
          </div>
          <div class="boxList">
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.ticket_time}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.phone}}</div>
            </div>
            <div class="listItem" v-if="detailInfo.user">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.user.id_card_number}}</div>
            </div>

            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.car_no}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.end_name}}</div>
            </div>
          </div>
          <div class="boxList">
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.pick_up_time}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.adult_numbers}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.remark}}</div>
            </div>
            <div class="listItem">
              <div style="white-space: nowrap;">:</div>
              <div>{{detailInfo.coupon_money/100}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="ModalBox">
        <Table :columns="columns15" :data="data11" border></Table>
      </div>
    </Modal>
  </div>

  <!--  -->
  <div v-else class="export_class">
    <div ref="pic">
      <div class="title_info">
        <div class="info_item">
          <p>{{exportInfo.ticket_time}}</p>
          <p>{{exportInfo.pick_up_time}}</p>
        </div>

        <div class="info_item">
          <p>{{exportInfo.driver_name}}</p>
          <p style="margin-left:110px">{{exportInfo.route}}</p>
        </div>

        <div class="info_item">{{exportInfo.car_no}}</div>
      </div>
      <!-- table -->
      <Table :columns="columns2" :data="data2" border>
      
      </Table>
    </div>

    <div class="export_btn" @click="exportPic('pic')"></div>
  </div>
</template>

<script>
import dispatchCenter from "./dispatchCenter";
export default dispatchCenter;
</script>
<style lang="less" scoped>
@import "./dispatchCenter.less";
</style>


